import React, { useState } from "react";
import {
  Box,
  Button,
  Grid,
  TextField,
  Typography,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from "@mui/material";
import { Scanner } from "@yudiel/react-qr-scanner";
import jsQR from "jsqr";
import toastMessage from "../../utils/toastMessage";
import { verifyUpiVpa, B2BBankTransfer } from "../../api/login/Login";

const UpiTransferPage = () => {
  const [verified, setVerified] = useState(false);
  const [qrHasAmount, setQrHasAmount] = useState(false);
  const [loading, setLoading] = useState(false);
  const [openQr, setOpenQr] = useState(false);
  const [openTpin, setOpenTpin] = useState(false);
  const [tpin, setTpin] = useState("");

  const [form, setForm] = useState({
    transfer_mode: "UPI",
    vpa: "",
    transfer_amount: "",
    beneficiary_name: "",
    beneficiary_account_name: "",
    beneficiary_email: "",
    beneficiary_mobile: "",
    beneficiary_add: "",
    beneficiary_city: "",
    beneficiary_state: "",
    beneficiary_pin_code: "",
  });

  const setField = (k, v) =>
    setForm((p) => ({ ...p, [k]: v }));

  const verifyUpi = async (vpa) => {
    try {
      setLoading(true);
      const res = await verifyUpiVpa({
        accountNumber: vpa,
        transfer_mode: "UPI",
      });

      if (res?.data?.success) {
        setVerified(true);
        setField("beneficiary_name", res.data.data?.name || "");
        setField(
          "beneficiary_account_name",
          res.data.data?.bank_name || ""
        );
        toastMessage(false, "UPI Verified");
      } else {
        toastMessage(true, res?.data?.message);
      }
    } catch {
      toastMessage(true, "UPI verification error");
    } finally {
      setLoading(false);
    }
  };

  const handleQrData = (raw) => {
    if (!raw?.toLowerCase().startsWith("upi://")) {
      toastMessage(true, "Invalid UPI QR");
      return;
    }

    const params = new URLSearchParams(raw.split("?")[1] || "");
    const vpa = params.get("pa");
    const amount = params.get("am");

    if (vpa) {
      const decodedVpa = decodeURIComponent(vpa);
      setField("vpa", decodedVpa);
      setVerified(false);
      verifyUpi(decodedVpa);
    }

    if (amount && !isNaN(amount)) {
      setField("transfer_amount", amount);
      setQrHasAmount(true);
    } else {
      setQrHasAmount(false);
    }

    setOpenQr(false);
  };


  const submitPayout = async () => {
    if (!verified) {
      toastMessage(true, "UPI verification required");
      return;
    }

    if (!/^\d{4}$/.test(tpin)) {
      toastMessage(true, "Invalid TPIN");
      return;
    }

    try {
      setLoading(true);

      const payload = {
        transfer_mode: "UPI",
        vpa: form.vpa,
        transfer_amount: Number(form.transfer_amount),
        beneficiary_name: form.beneficiary_name,
        beneficiary_account_name: form.beneficiary_account_name,
        beneficiary_email: form.beneficiary_email,
        beneficiary_mobile: form.beneficiary_mobile,
        beneficiary_add: form.beneficiary_add,
        beneficiary_city: form.beneficiary_city,
        beneficiary_state: form.beneficiary_state,
        beneficiary_pin_code: form.beneficiary_pin_code,
        tpin,
      };

      const res = await B2BBankTransfer(payload);

      if (res?.data?.success) {
        toastMessage(false, "UPI Payout Successful");
        reset();
      } else {
        toastMessage(true, res?.data?.message);
      }
    } finally {
      setLoading(false);
    }
  };

  const reset = () => {
    setVerified(false);
    setQrHasAmount(false);
    setOpenTpin(false);
    setTpin("");
    setForm({
      transfer_mode: "UPI",
      vpa: "",
      transfer_amount: "",
      beneficiary_name: "",
      beneficiary_account_name: "",
      beneficiary_email: "",
      beneficiary_mobile: "",
      beneficiary_add: "",
      beneficiary_city: "",
      beneficiary_state: "",
      beneficiary_pin_code: "",
    });
  };

  return (
    <>
      <Box p={3} bgcolor="#fff" borderRadius={2}>
        <Typography variant="h6" mb={2}>
          UPI Transfer
        </Typography>

        <Grid container spacing={2}>
          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              className="mui-autofill-fix"
              label="UPI ID"
              value={form.vpa}
              disabled={verified || loading}
              onChange={(e) => {
                setField("vpa", e.target.value.toLowerCase());
                setVerified(false);
              }}
            />

            {!verified && (
              <Box mt={1} display="flex" gap={1}>
                <Button
                  onClick={() => setOpenQr(true)}
                  disabled={loading}
                >
                  Scan QR
                </Button>
                <Button
                  onClick={() => verifyUpi(form.vpa)}
                  disabled={!form.vpa || loading}
                >
                  {loading ? "Verifying..." : "Verify"}
                </Button>
              </Box>
            )}
          </Grid>

          {verified && (
            <>
              {/* ✅ Beneficiary Name + Verified */}
              <Grid item xs={12} md={4}>
                <TextField
                  fullWidth
                  label="Beneficiary Name"
                  className="mui-autofill-fix"
                  value={form.beneficiary_name}
                  disabled
                />
                <Typography
                  variant="caption"
                  sx={{ color: "green", fontWeight: 600 }}
                >
                  ✔ Verified
                </Typography>
              </Grid>

              {/* ✅ Bank Name */}
              <Grid item xs={12} md={4}>
                <TextField
                  fullWidth
                  label="Bank Name"
                  className="mui-autofill-fix"
                  value={form.beneficiary_account_name}
                />
              </Grid>

              {/* ✅ Amount */}
              <Grid item xs={12} md={4}>
                <TextField
                  fullWidth
                  label="Amount"
                  className="mui-autofill-fix"
                  value={form.transfer_amount}
                  disabled={loading || qrHasAmount}
                  onChange={(e) =>
                    setField("transfer_amount", e.target.value)
                  }
                />
              </Grid>

              {/* ✅ Other editable fields */}
              {[
                ["beneficiary_email", "Email"],
                ["beneficiary_mobile", "Mobile"],
                ["beneficiary_add", "Address"],
                ["beneficiary_city", "City"],
                ["beneficiary_state", "State"],
                ["beneficiary_pin_code", "PIN Code"],
              ].map(([key, label]) => (
                <Grid item xs={12} md={4} key={key}>
                  <TextField
                    fullWidth
                    className="mui-autofill-fix"
                    label={label}
                    value={form[key]}
                    disabled={loading}
                    onChange={(e) => setField(key, e.target.value)}
                  />
                </Grid>
              ))}
            </>
          )}


        </Grid>

        {verified && (
          <Box mt={3} textAlign="center">
            <Button
              variant="contained"
              disabled={loading}
              onClick={() => setOpenTpin(true)}
            >
              Submit
            </Button>
          </Box>
        )}
      </Box>

      <Dialog open={openQr} maxWidth="xs" fullWidth>
        <DialogTitle align="center">Scan UPI QR</DialogTitle>
        <DialogContent>
          <Scanner
            constraints={{ facingMode: "environment" }}
            onScan={(r) => {
              const raw =
                typeof r === "string"
                  ? r
                  : r?.rawValue || r?.[0]?.rawValue;
              if (raw) handleQrData(raw);
            }}
          />
          <Box mt={2} textAlign="center">
            <Button component="label" variant="outlined" disabled={loading}>
              Upload QR
              <input
                hidden
                type="file"
                accept="image/*"
                onChange={(e) => {
                  const f = e.target.files?.[0];
                  if (!f) return;
                  const img = new Image();
                  img.src = URL.createObjectURL(f);
                  img.onload = () => {
                    const c = document.createElement("canvas");
                    const x = c.getContext("2d");
                    c.width = img.width;
                    c.height = img.height;
                    x.drawImage(img, 0, 0);
                    const d = x.getImageData(0, 0, c.width, c.height);
                    const code = jsQR(d.data, d.width, d.height);
                    if (code?.data) handleQrData(code.data);
                  };
                }}
              />
            </Button>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenQr(false)} disabled={loading}>
            Close
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog open={openTpin}>
        <DialogTitle align="center">Enter TPIN</DialogTitle>
        <DialogContent>
          <TextField
            fullWidth
            className="mui-autofill-fix"
            type="password"
            value={tpin}
            inputProps={{ maxLength: 4 }}
            disabled={loading}
            onChange={(e) =>
              /^\d{0,4}$/.test(e.target.value) && setTpin(e.target.value)
            }
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenTpin(false)} disabled={loading}>
            Cancel
          </Button>
          <Button onClick={submitPayout} disabled={loading}>
            {loading ? "Processing..." : "Confirm"}
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default UpiTransferPage;
