import { toast } from "react-toastify";


export default function toastMessage(error, message = "something went wrong") {

    error === false ? toast.success(message) : toast.error(message);
    // error === false  ?  setSnackbar({ open: true, message: response?.data?.message ||, severity: 'success' }) :  setSnackbar({ open: true, message , severity: 'error' });
}